import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the MiRutinaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@IonicPage()
@Component({
  selector: 'page-mi-rutina',
  templateUrl: 'mi-rutina.html',
})
export class MiRutinaPage {

idUs:integer;

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events, private alertCtrl: AlertController) {


  events.subscribe('updateRutin', () => {
    this.apiProvider.verificarLogin()
    .then(data => {    
    this.apiProvider.getRutinaUsuario(data.idUsuario)
      .then(data => {
      this.proximoEjercicio = data[1][0];
      this.events.publish('registrarNuevoEjercicio',this.proximoEjercicio );
      this.diasRutina =  (Object.values(data[0]));
       console.log(this.diasRutina);
       console.log(this.proximoEjercicio);


      });
     });
  });




  }

  ionViewDidLoad() {

    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();

    this.apiProvider.verificarLogin()
    .then(data => {    
      this.idUs=data.idUsuario;

      this.apiProvider.getRutinaUsuario(data.idUsuario)
      .then(data => {
    
      this.proximoEjercicio = data[1][0];
      this.diasRutina =  (Object.values(data[0]));
       console.log(this.diasRutina);
      loading.dismissAll();

      console.log(this.proximoEjercicio);


      });



     });


    }
    solicitarRutinaApis() {
    console.log(this.idUs);


    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();
    
 
  
       this.apiProvider.solicitarRR(this.idUs)
      .then(data => {
    
     if(data.data){
     this.presentAlert('Rutina solicitada','Excelente! Tener un plan de ejercicios es el primer paso para cumplir tus metas! Un instructor te asignara la rutina en breve');
     }
     else{
      this.presentAlert('Error','Ha ocurrido un error inesperado');
     }

            loading.dismissAll();
      });






  }

  presentAlert(titulo,mensaje) {
    let alert = this.alertCtrl.create({
      title: titulo,
      subTitle: mensaje,
      buttons: ['Cerrar']
    });
    alert.present();
  }



getDays(i){
  var texto = '';
   this.diasRutina[i].forEach(function(element, index) {

    if(element.completado == null){
    texto += '<br><div class="alertData">'+(index+1)+'. '+element.nombre+'</div>';
    }
    else{
    texto += '<br><div class="alertData">'+(index+1)+'. '+element.nombre+'<img class="completado" src="assets/imgs/chec.png"/></div>';
    }
    
    });

  return texto;
}


      mostrarInfoDias(inde){
    this.presentAlert('Dia '+(inde+1), this.getDays(inde));
  
  }




    getPorcentaje(dia){
        let num = 0;
    dia.forEach(function(item){
       if(item.completado) {num++}
    });

    let enviar = ((num*100)/(dia.length)) + '%';
    return enviar;
       
  
  }




  goActividad(){
  //console.log(this.proximoEjercicio.idActividad);
  this.navCtrl.push('ActividadPage',{idActividad:this.proximoEjercicio.idActividad, idR:this.proximoEjercicio.idRutinaActividad, diaNumero:this.proximoEjercicio.diaNumero});
  }


}



